<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <div class="d-flex align-center">
        <v-img
          alt="Culoud Logo"
          class="shrink mr-2"
          contain
          :src="require('@/assets/logo.png')"
          transition="scale-transition"
          width="40"
        />
      </div>
      <v-btn text v-if="!isLogin">
        <span class="mr-2" @click="login">ログイン</span>
      </v-btn>
      <v-btn text v-if="!isLogin">
        <span class="mr-2" @click="register">新しく登録</span>
      </v-btn>
      <v-btn text v-if="isLogin">
        <span class="mr-2" @click="createRoom">部屋を作成</span>
      </v-btn>
      <v-btn text v-if="isLogin">
        <span class="mr-2" @click="logout">ログアウト</span>
      </v-btn>
      <template v-slot:extension v-if="isLogin">
        <v-tabs v-model="tabModel" align-with-title>
          <v-tab href="#home">Home</v-tab>
          <v-tab v-for="room in rooms"
          :key="room.created_at"
          :href="'#'+room.id">
            {{ room.name }}
          </v-tab>
        </v-tabs>
      </template>
    </v-app-bar>
    <v-main>
      <v-tabs-items v-model="tabModel" v-if="isLogin">
        <v-tab-item value="home">
          Home!!!
        </v-tab-item>
        <v-tab-item v-for="room in rooms" :key="room.created_at" :value="room.id">
         this is {{ room.name }}
        </v-tab-item>
      </v-tabs-items>
      <NoticeDialog ref="notice" />
      <LoginDialog ref="login" @done="afterLogin" />
      <RegisterDialog ref="register" @done="afterRegister" />
      <CreateRoomDialog ref="createRoom" @done="afterCreateRoom" />
    </v-main>
  </v-app>
</template>

<script>
import io from 'socket.io-client';
import LoginDialog from '@/components/LoginDialog.vue';
import RegisterDialog from '@/components/RegisterDialog.vue';
import CreateRoomDialog from '@/components/CreateRoomDialog.vue';
import NoticeDialog from '@/components/NoticeDialog.vue';

export default {
  name: 'App',

  components: {
    NoticeDialog,
    LoginDialog,
    RegisterDialog,
    CreateRoomDialog,
  },
  mounted() {
    this.ws.on('rooms', (value) => {
      this.rooms = value.rooms;
    });
    this.ws.on('error', () => {

    });
    this.ws.on('hello', (value) => {
      this.chat[value.room].push(value);
    });
    this.ws.on('message', (value) => {
      this.chat[value.room].push(value);
    });
    this.ws.on('notice', (value) => {
      this.$refs.notice.emit('open', value.message);
    });
    this.ws.on('error', (value) => {
      this.$refs.notice.emit('open', value.message);
    });
    this.ws.on('login', (value) => {
      this.id = value.id;
      this.isLogin = value.login;
    });
  },
  methods: {
    login() {
      this.$refs.login.$emit('open');
    },
    afterLogin(id, password) {
      // console.log(id, password);
      this.ws.emit('login', { id, password });
      // this.isLogin = true;
    },
    register() {
      this.$refs.register.$emit('open');
    },
    afterRegister(name, id, password, email) {
      // console.log(id, password, email);
      this.ws.emit('register', {
        name, id, password, email,
      });
      // this.islogin = true;
    },
    logout() {
      this.ws.emit('logout');
    },
    afterLogout() {
      this.isLogin = false;
    },
    createRoom() {
      this.$refs.createRoom.$emit('open');
    },
    afterCreateRoom(name) {
      this.ws.emit('create_room', { name });
      this.rooms.push({ id: name, name });
    },
    join() {

    },
    leave() {

    },
    send() {

    },
    invite() {

    },
  },
  data() {
    return {
      id: '',
      domain: '',
      ws: io('localhost:5000/api/ws'),
      isLogin: false,
      userName: '',
      rooms: [{ id: 'dummy', name: 'dummy' }],
      chat: { dummy: [{ user: 'guest', content: 'Hello!' }] },
      tabModel: 'home',
    };
  },

};
</script>
